import React from "react";
import "./About.css";

const About = () => {
  return (
    <div className="about-div" id="about">
      <div className="about-image">
        <img
          src={require("../../assets/Ilustration 1.png")}
          alt="illustration"
        />
      </div>
      <div className="about-us">
        <h6 className="about-company">OUR COMPANY</h6>
        <h3 className="about-header">
          Some Fine
          Words About Us
        </h3>
        <p className="about-summary">
        Welcome to Simaka Engineering Services Limited
At Simaka Engineering Services Limited, we are at the forefront of innovation,
 delivering cutting-edge engineering solutions to meet the evolving needs of our clients. 
 With a rich history spanning 10 years, we have established ourselves as a trusted partner in the 
 engineering industry, providing a wide range of services tailored to various sectors.
        </p>
        <span className="arrows">
          <i className="fas fa-arrow-left small-icon" />
          <i className="fas fa-arrow-right long-icon" />
        </span>
      </div>
    </div>
  );
};

export default About;
